* {

  font-family: Verdana, Geneva, Tahoma, sans-serif;

}

body {

  padding: 60px;

}

input{
  
  color: rgb(77, 77, 77);
  font-size: 1.2rem;
  font-style: italic;
  width: 100%;
  border-radius: 5px;
  
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {

  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  

}

.card p {
  padding:5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}


